<template>
  <footer class="footer text-center py-5" style="background-image: url('./img/bg.jpg');">
    <div class="container">
      <div class="row align-items-center justify-content-between">
        <span class="footer_logo mb-3 col-xl col-lg-3 text-lg-start">
          <div class="ps-lg-5"><img src="@/assets/logo.svg" alt="" class="svg"></div>
        </span>
        <div class="mb-3 col-xl-auto col-lg">
          <span class="d-block d-lg-inline-block me-lg-4">
            宜蘭縣冬山鄉大進村大進路466號
          </span>
          <span class="d-block d-sm-inline-block me-sm-3">
            訂購專線 : (03)961-3676
          </span>
          <span class="d-block d-sm-inline-block">
            傳真 : (03)951-2752
          </span>
        </div>
      </div>
      <div class="copy-right">
        Copyright © 2022 Full Foods Co., Ltd. All right reserved.
      </div>
    </div>
  </footer>
  <div class="goTop iconfont ml-gotop" :class="isTop ? 'goTopAfter' : ''" @click="goTop()"><span class="topicon">TOP</span></div>
</template>
<script>
export default {
  data () {
    return {
      scrollNum: 0,
      isTop: false
    }
  },
  methods: {
    goTop () {
      document.documentElement.scrollTop = 0
    }
  },
  mounted () {
    window.addEventListener('scroll', () => {
      const top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
      this.scrollNum = top
      if (top >= 800) {
        this.isTop = true
      } else {
        this.isTop = false
      }
    })
  }
}
</script>
