import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/UserBoard.vue'

const routes = [
  {
    path: '/',
    component: Home,
    children: [
      {
        name: '',
        path: '',
        component: () => import('../views/Home.vue')
      },
      {
        name: 'Prd',
        path: 'products/:categoryName?',
        component: () => import('../views/UserProducts.vue')
      },
      {
        name: 'PrdDetail',
        path: 'product/:productId',
        component: () => import('../views/UserPrdDetail.vue')
      },
      {
        name: 'News',
        path: 'news',
        component: () => import('../views/UserNews.vue')
      },
      {
        name: 'NewsDetail',
        path: 'news/:newsId',
        component: () => import('../views/UserNewsDetail.vue')
      },
      {
        name: 'Login',
        path: 'login',
        component: () => import('../views/UserLogin.vue')
      },
      {
        name: 'Register',
        path: 'register',
        component: () => import('../views/UserRegister.vue')
      },
      {
        name: 'Forget',
        path: 'forget',
        component: () => import('../views/UserForget.vue')
      },
      {
        name: 'About',
        path: 'about',
        component: () => import('../views/UserAbout.vue')
      },
      {
        name: 'Notice',
        path: 'notice',
        component: () => import('../views/UserNotice.vue')
      },
      {
        name: 'Cart',
        path: 'cart',
        component: () => import('../views/UserCart.vue')
      },
      {
        name: 'Completed',
        path: 'completed/:orderId',
        component: () => import('../views/UserCompleted.vue')
      }
    ]
  },
  {
    path: '/login_',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/dashboard',
    component: () => import('../views/Dashboard.vue'),
    children: [
      {
        path: 'products',
        component: () => import('../views/Products.vue')
      },
      {
        path: 'orders',
        component: () => import('../views/Orders.vue')
      },
      {
        path: 'coupons',
        component: () => import('../views/Coupons.vue')
      },
      {
        path: 'articles',
        component: () => import('../views/Articles.vue')
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    component: () => import('../views/NotFound.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  linkActiveClass: 'active',
  routes,
  scrollBehavior (to, from, savedPosition) {
    return new Promise((resolve, reject) => {
      if (to.name !== 'Prd') {
        setTimeout(() => {
          resolve({ left: 0, top: 0, behavior: 'smooth' })
        }, 0)
      }
    })
  }
})

export default router
