<template>
  <div class="toast-container position-fixed p-3 end-0">
    <Toast v-for="(msgOut, key) in messages" :key='key' :msg='msgOut'></Toast>
  </div>
</template>
<script>
import Toast from '../components/Toast.vue'
export default {
  components: {
    Toast
  },
  data () {
    return {
      messages: []
    }
  },
  inject: ['emitter'],
  mounted () {
    this.emitter.on('push-message', (data) => {
      const { style = 'success', title, content } = data
      this.messages.push({ style, title, content })
    })
  }
}
</script>
