<template>
  <header ref="header" style="background-image: url('./img/bg.jpg')">
      <div class="wsmenucontent overlapblackbg" :class="{'menuopen':menuState,'menuclose':!menuState}" @click.prevent="menuState = !menuState"></div>
      <div class="row wrap align-items-center">
          <div class="col">
            <nav class="wsmenu slideLeft" :class="{'menuopen':menuState,'menuclose':!menuState}" style="background-image: url('./img/bg.jpg')">
              <router-link to="/login" class="login_btn d-flex d-xl-none">
                <img :src="`./img/user.svg`" alt="" class="me-2"> 會員登入
              </router-link>
              <ul class="mobile-sub wsmenu-list" itemscope itemtype="http://www.schema.org/SiteNavigationElement">
                <li v-for="item in items" :class="{'active':item.isOpen}" :key="item">
                  <span class="wsmenu-click" @click.prevent="(item.isOpen = !item.isOpen)" v-if="item.child"><i class="wsmenu-arrow fa fa-angle-down d-inline-flex d-xl-none"></i></span>
                  <router-link class="nav-link" :to="item.href" v-if="!item.otherLink">
                    {{ item.title }}
                  </router-link>
                  <a :href="item.href" class="nav-link" target="_blank" v-else>
                    {{ item.title }}
                  </a>
                </li>
              </ul>
            </nav>
          </div>
          <div class="col-auto logo_out">
            <div>
              <router-link to="/" class="logo">
                <img class="svg" src="@/assets/logo.svg" alt="">
              </router-link>
            </div>
            <a id="navToggle" class="animated-arrow slideLeft d-xl-none" :class="{'menuopen':menuState,'menuclose':!menuState}" @click.prevent="menuState = !menuState"><span></span></a>
          </div>
          <div class="col d-flex justify-content-end align-items-center position-relative">
            <router-link to="/login" class="header_a d-none d-xl-inline-block me-4 position-relative">
              會員登入
            </router-link>
            <div class="dropdown cart_box pe-2 pe-md-3 pe-xl-0">
              <div id="cart" ref="dropdown" data-bs-toggle="dropdown" data-bs-auto-close="outside" aria-expanded="false">
                <span>{{cartNum}}</span>
                <svg-icon name="cart"/>
              </div>
              <div id="cartInfo-wrap" class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuClickableInside">
                <div class="wrapper">
                  <template v-if="cartNum != 0">
                    <div class="cartList">
                      <div class="row_" v-for="item in cart.carts" :key="item.id">
                        <div class="img"><img :src="item.product.imageUrl" alt=""><span>{{item.product.title}}</span></div>
                        <div class="quantity">數量 : {{item.qty}}</div>
                        <div class="price">$ {{item.total}}</div>
                      </div>
                    </div>
                    <div class="total">
                      <span>總計 : $ {{cart.final_total}}</span>
                      <a class="btn btn-primary" href="#" @click.prevent="goCartPage">結帳</a>
                    </div>
                  </template>
                  <div v-else class="text-center">
                    <h6 class="my-4">購物車無商品!</h6>
                    <a href="#" class="btn btn-primary" @click.prevent="goPrd">繼續選購</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
  </header>
</template>

<script>
import Dropdown from 'bootstrap/js/dist/dropdown'
export default {
  data () {
    return {
      menuState: false,
      items: [],
      isActive: false,
      dropdown: {},
      cart: [],
      cartNum: 0
    }
  },
  inject: ['emitter'],
  methods: {
    getData () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.cart = res.data.data
          let total = 0
          this.cart.carts.forEach(function (item) {
            total += item.qty
          })
          this.cartNum = total
        }
      })
    },
    goCartPage () {
      this.dropdown.hide()
      this.$router.push('/cart')
    },
    goPrd () {
      this.dropdown.hide()
      this.$router.push('/products')
    }
  },
  mounted () {
    const vm = this
    const url = './json/menu.json'
    this.$http.get(url).then((response) => {
      // console.log(response.data)
      vm.items = response.data
    })
    this.dropdown = new Dropdown(this.$refs.dropdown)
    this.getData()
  },
  watch: {
    $route () {
      if (this.menuState === true) {
        this.menuState = false
      }
    }
  },
  created () {
    this.emitter.on('getData', () => {
      this.getData()
    })
  }
}
</script>
